<template>
    <LayoutAuthenticated>
            <!-- LOADING -->
            <SectionMain v-if="loading">
              <LoadingComponent />
          </SectionMain>

      <!-- LOADING END -->
    <SectionMain v-else>
        <BreadCrumb :projectName="dashboard.name" class="py-4" />
        <ProjectHead :cardBoxWidgets="cardBoxWidgets" />

        <SectionTitleLineWithButton :icon="mdiViewDashboardVariant" title="Market Overview"> </SectionTitleLineWithButton>
        <CardBox has-table>
            <TableMarketCap :pagesize="10"/>
        </CardBox>

        <SectionTitleLineWithButton :icon="mdiViewDashboardVariant" title="Total Value Locked & Fees"></SectionTitleLineWithButton>
        <div class=" mb-6 flex overflow-x-auto">
          <CardBox has-table class="w-1/2 mr-2">
            <TableTVLFee :type="'tvl'" :pagesize="5"/>
          </CardBox>        
          <CardBox has-table class="w-1/2 mr-2">
            <TableTVLFee :type="'fee_total24h'" :pagesize="5"/>
          </CardBox>
        </div>

    </SectionMain> 
    </LayoutAuthenticated>
  </template>
  
<script>
import LoadingComponent from "@/components/LoadingComponent.vue";
import LayoutAuthenticated from "@/layouts/LayoutAuthenticated.vue";
import SectionMain from "@/components/SectionMain.vue";
import BreadCrumb from "@/components/BreadCrumb.vue";
import SectionTitleLineWithButton from "@/components/SectionTitleLineWithButton.vue";
import ProjectHead from "@/components/ProjectHead.vue";
import CardBox from "@/components/CardBox.vue";
import TableMarketCap from "@/components/TableMarketCap.vue";
import TableTVLFee from "@/components/TableTVLFee.vue";
import {
mdiViewDashboardVariant,
} from "@mdi/js";
import { supabase } from "../supabase";

export default {
  components: {
    LayoutAuthenticated,
    SectionMain,
    BreadCrumb,
    SectionTitleLineWithButton,
    ProjectHead,
    CardBox,
    TableMarketCap,
    TableTVLFee,
    LoadingComponent
  },
  name: "GeneralView",
  data() {
    return {
      mdiViewDashboardVariant,
      asset: {},
      loading: true,

    dashboard:
      {
        name: "Market overview",
      }
  }
},
async created() {
  // Fetch data when the component is first created
  this.fetchMarketData();
},
methods: {
  async fetchMarketData() {
    let { data: marketdata, error } = await supabase
    .from('marketdata')
    .select()
    .order('id', { ascending: false })

    console.log('marketData; ', marketdata)
    
    this.asset = marketdata[0]
    this.loading = false
    console.log('asset: ', this.asset)

    if (error) return console.error("result" + error);

    
    // Update the card box widgets data
    this.cardBoxWidgets = [
    {
      prefix: "$",
      trend: this.asset.total_market_cap_yesterday_percentage_change ? parseFloat(this.asset.total_market_cap_yesterday_percentage_change).toFixed(2) + "%" : "N/A",
      trendType: this.asset.total_market_cap_yesterday_percentage_change ? (this.asset.total_market_cap_yesterday_percentage_change >= 0 ? "up" : "down") : "info",
      color: "text-emerald-500",
      label: "Total Marketcap",
      suffix: this.asset.total_market_cap >= 1000000000 ? "B" : "M",
      number: this.asset.total_market_cap >= 1000000000 ? (this.asset.total_market_cap / 1000000000).toFixed(0) : (this.asset.total_market_cap / 1000000).toFixed(0),
      icon: "https://cdn0.iconfinder.com/data/icons/cryptocurrency-63/64/Market-capitalization-global-bitcoin-cryptocurrency-512.png"
    },
    {
      prefix: "$",
      trend: this.asset.stablecoin_24h_percentage_change ? parseFloat(this.asset.stablecoin_24h_percentage_change).toFixed(2) + "%" : "N/A",
      trendType: this.asset.stablecoin_24h_percentage_change ? (this.asset.stablecoin_24h_percentage_change >= 0 ? "up" : "down") : "info",
      color: "text-emerald-500",
      label: "Stables Marketcap",
      suffix: this.asset.stablecoin_market_cap >= 1000000000 ? "B" : "M",
      number: this.asset.stablecoin_market_cap >= 1000000000 ? (this.asset.stablecoin_market_cap / 1000000000).toFixed(0) : (this.asset.stablecoin_market_cap / 1000000).toFixed(0),
      icon: "https://cdn3.iconfinder.com/data/icons/start-up-4/44/money-512.png"
    },
    {
      prefix: "$",
      trend: this.asset.defi_24h_percentage_change ? parseFloat(this.asset.defi_24h_percentage_change).toFixed(2) + "%" : "N/A",
      trendType: this.asset.defi_24h_percentage_change ? (this.asset.defi_24h_percentage_change >= 0 ? "up" : "down") : "info",
      color: "text-emerald-500",
      label: "DeFi Marketcap",
      suffix: this.asset.defi_market_cap >= 1000000000 ? "B" : "M",
      number: this.asset.defi_market_cap >= 1000000000 ? (this.asset.defi_market_cap / 1000000000).toFixed(0) : (this.asset.defi_market_cap / 1000000).toFixed(0),
      icon: "https://cdn4.iconfinder.com/data/icons/gamefi-3/512/Gamefi-game-nftgame-defi-nft-blockchaingame-playtoearn-p2e-512.png"
    },
    {
      prefix: "",
      trend: this.asset.btc_dominance_24h_percentage_change ? parseFloat(this.asset.btc_dominance_24h_percentage_change).toFixed(2) + "%" : "N/A",
      trendType: this.asset.btc_dominance_24h_percentage_change ? (this.asset.btc_dominance_24h_percentage_change >= 0 ? "up" : "down") : "info",
      color: "text-emerald-500",
      label: "BTC Dominance",
      suffix: "%",
      number: parseFloat(this.asset.btc_dominance).toFixed(2),
      icon: "https://cdn2.iconfinder.com/data/icons/bitcoin-and-mining/44/coins-512.png"
    },
  ]
  },
},
}
</script>